
































import { Component, Vue, Prop, Watch } from 'vue-property-decorator'
import VueRouter from 'vue-router'
import page from '@/components/common-components/page.vue'
import pageView from '@/components/common-ui/page-view.vue'
import { deepClone } from '@/until/index'
@Component({
  components: {
    page,
    pageView
  }
})

export default class noticeConfig extends Vue {
  public $router: VueRouter | undefined
  public $route: any
  private className: string = '点位选择'

  private showBtn = false;

  private coordinate = []


  private hidePopup (refId) {
    const component: any = this.$refs[refId]
    component.hide()
  }

  private createMap (coord) {
    // let iframeLabel = document.createElement('iframe');
    // iframeLabel.setAttribute('allow', 'midi; geolocation; microphone; camera; display-capture; encrypted-media;');
    // iframeLabel.setAttribute('sandbox', 'allow-modals allow-forms allow-scripts allow-same-origin allow-popups allow-top-navigation-by-user-activation allow-downloads')
    // iframeLabel.src = `https://mapapi.qq.com/web/mapComponents/locationPicker/v/index.html?search=1&type=1&key=XDNBZ-GJLLD-EUR43-HDZDI-L2M4E-73FTQ&referer=myapp`;
    // if (coord) {
    //   iframeLabel.src = iframeLabel.src + '&coord=' + coord 
    // }
    // console.log(iframeLabel.src, 'cccccccc')
    // iframeLabel.id = 'map_box'
    // iframeLabel.width = '100%'
    // iframeLabel.height = '100%'
    // iframeLabel.frameborder = '0'
    // document.getElementById('map-box').appendChild(iframeLabel)
    const head = document.getElementsByTagName('head')[0]
    const script1 = document.createElement('script')
    script1.type = 'text/javascript'
    script1.src = 'https://open.mobile.qq.com/sdk/qqapi.js'
    script1.onload = () => {
      
      const script = document.createElement('script')
      script.type = 'text/javascript'
      script.src = 'https://map.qq.com/api/gljs?v=1.exp&key=XDNBZ-GJLLD-EUR43-HDZDI-L2M4E-73FTQ&libraries=tools'
      script.onload = () => {
        // 回调函数
          var map; // 地图
          var editor; // 编辑器
          var activeType = 'marker'; // 激活的图形编辑类型

          // 切换激活图层
          document.getElementById('toolControl').addEventListener('click', (e) => {
            console.log('jjjjjjjjjjjjjjjjjjjjjjjjjjjjjj')
            editor.setActiveOverlay('polygon');
          });
          map = new TMap.Map('region-box', {
          zoom: 12, // 设置地图缩放级别
          center: new TMap.LatLng(39.984104, 116.307503), // 设置地图中心点坐标
        });

        // 初始化几何图形及编辑器
        var marker = new TMap.MultiMarker({
          map: map,
        });
        var polyline = new TMap.MultiPolyline({
          map: map,
        });
        var polygon = new TMap.MultiPolygon({
          map: map,
        });
        var circle = new TMap.MultiCircle({
          map: map,
        });
        var rectangle = new TMap.MultiRectangle({
          map: map,
        });
        var ellipse = new TMap.MultiEllipse({
          map: map,
        });
        editor = new TMap.tools.GeometryEditor({
          // TMap.tools.GeometryEditor 文档地址：https://lbs.qq.com/webApi/javascriptGL/glDoc/glDocEditor
          map: map, // 编辑器绑定的地图对象
          overlayList: [
            // 可编辑图层 文档地址：https://lbs.qq.com/webApi/javascriptGL/glDoc/glDocEditor#4
            {
              overlay: marker,
              id: 'marker',
            },
            {
              overlay: polyline,
              id: 'polyline',
            },
            {
              overlay: polygon,
              id: 'polygon',
            },
            {
              overlay: circle,
              id: 'circle',
            },
            {
              overlay: rectangle,
              id: 'rectangle',
            },
            {
              overlay: ellipse,
              id: 'ellipse',
            },
          ],
          actionMode: TMap.tools.constants.EDITOR_ACTION.DRAW, // 编辑器的工作模式
          activeOverlayId: 'marker', // 激活图层
          snappable: true, // 开启吸附
        });
        // 监听绘制结束事件，获取绘制几何图形
        editor.on('draw_complete', (geometry) => {
          // 判断当前处于编辑状态的图层id是否是overlayList中id为rectangle（矩形）图层
          console.log(geometry, 'yyyyyyyyyyyyyyyyyyyyyyyyyyy')
        });
      }
      head.appendChild(script)
    }
    head.appendChild(script1)
  }

  private saveSchoolInfo () {
    let backData = this.$store.state.backData;
    if (!backData) {
      const toast = (this as any).$createToast({
        txt: '请选择坐标',
        type: 'correct'
      })
      toast.show()
      return
    }
    this.$router.back();
  }

  backInfo = {}

  mounted () {
    let that = this
    let query = this.$route.query
    this.backInfo = query.backInfo
    let location = query.location
    let coord = null;
    console.log(query, 'sssssssssssssssssss')
    if (location) {
      coord = location.join(',')
    }
    that.createMap(coord)
    window.addEventListener('message', function (event) {
      // 接收位置信息，用户选择确认位置点后选点组件会触发该事件，回传用户的位置信息
      var loc = event.data;
      if (loc && loc.module == 'locationPicker') {//防止其他应用也会向该页面post信息，需判断module是否为'locationPicker'
        let latlng = loc.latlng
        that.$store.commit('SET_BACK_DATA', {
          route: 'point',
          data: {
            type: 'start',
            coordinate: [latlng.lng, latlng.lat],
            label: loc.poiname || loc.poiaddress,
            backInfo: that.backInfo
          }
        })
      }
    }, false);
  }

  showToastType () {
    const toast = (this as any).$createToast({
      txt: '保存成功',
      type: 'correct'
    })
    toast.show()
  }

}
